.nav__link {
  color: #1d1d1d;
  text-decoration: none;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: 2px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 32px;
  cursor: url("../../images/cursor-mouse-mice-bl.png"), auto;

}

.nav__link:hover {
  border-bottom: 1px solid #1d1d1d;
}
